.contact1 {
  position: relative;
  font-size: var(--font-size-5xl);
  font-weight: 600;
  font-family: var(--font-poppins);
  color: var(--color-gray-100);
  text-align: left;
}

.firstname,
.lastname {
  border: 0;
  background-color: var(--color-gray-100);
  flex: 1;
  position: relative;
  border-radius: var(--br-xs);
}

.firstname {
  align-self: stretch;
}

.lastname {
  height: 50px;
}

.email,
.firstname-parent {
  align-self: stretch;
  height: 50px;
  flex-shrink: 0;
}

.firstname-parent {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-3xs);
}

.email {
  border: 0;
  background-color: var(--color-gray-100);
  border-radius: var(--br-xs);
}

.email,
.msg,
.submit {
  position: relative;
}

.msg {
  border: 0;
  background-color: var(--color-gray-100);
  align-self: stretch;
  border-radius: var(--br-2xs);
  height: 100px;
  flex-shrink: 0;
}

.submit {
  font-size: var(--font-size-3xl);
  font-weight: 600;
  font-family: var(--font-roboto);
  color: var(--color-gray-100);
  text-align: left;
}

.button2,
.contactform {
  display: flex;
  align-items: center;
}

.button2 {
  cursor: pointer;
  border: 0;
  padding: var(--padding-4xs) var(--padding-sm);
  background-color: var(--color-crimson);
  border-radius: var(--br-xl);
  flex-direction: row;
  justify-content: center;
}

.contactform {
  flex: 1;
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-2xs);
}

.footer-child {
  align-self: stretch;
  flex: 1;
  position: relative;
  background-image: url(../images/rectangle162@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}

.footer {
  align-self: stretch;
  background-color: var(--color-gray-200);
  display: flex;
  flex-direction: row;
  padding: var(--padding-lg) var(--padding-5xl);
  align-items: center;
  justify-content: center;
  gap: var(--gap-3xl);
}

@media screen and (max-width: 960px) {

  .contactform,
  .footer-child {
    flex: unset;
    align-self: stretch;
  }

  .footer {
    flex-direction: column;
    padding: 15px;
    gap: 10px;

  }
}