.team-icon {
  align-self: stretch;
  flex: 1;
  position: relative;
  border-radius: 45px;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.team-officials,
.team-photo {
  align-self: stretch;
  flex-direction: row;
}
.team-photo {
  display: flex;
  padding: var(--padding-4xs) var(--padding-2xl);
  align-items: center;
  justify-content: center;
}
.team-officials {
  padding: 0 var(--padding-2xl);
  gap: 25px;
}
.members-area,
.team,
.team-admins,
.team-officials {
  display: flex;
  align-items: center;
  justify-content: center;
}
.team-admins {
  align-self: stretch;
  flex-direction: row;
  padding: 0 var(--padding-2xl);
  gap: var(--gap-xs);
}
.members-area,
.team {
  flex-direction: column;
  gap: var(--gap-3xs);
  align-self: stretch;
}
.team {
  position: relative;
  background-color: var(--color-gray-100);
  width: 100%;
  overflow: hidden;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-xl);
}
@media screen and (max-width: 1200px) {
  .team-admins,
  .team-officials {
    flex-direction: column;
  }
  .team-admins {
    align-items: center;
    justify-content: center;
    padding: 3px;
  }
}
@media screen and (max-width: 960px) {
  .team-admins {
    flex-direction: column;
  }
  .team{
    gap: 0;
  }
  #team-container{
    padding: 10px 4px;
  }
  .team-officials{
    padding: 15px;
  }
  .team-members{
    width: 85%;
  }
  .crc img{
    animation: 1s ease 0s 1 normal forwards slide-in-left;
  }
  .teammember{
    animation: 1s ease 0s 1 normal forwards slide-in-right;
  }
}
