.logo-icon {
  position: relative;
  width: 145px;
  height: 93px;
  flex-shrink: 0;
  object-fit: cover;
}
.logo-container {
  display: flex;
  flex-direction: column;
  padding: 0 var(--padding-xl) 0 var(--padding-3xl);
  align-items: center;
  justify-content: center;
}
.home1 {
  text-decoration: none;
  position: relative;
  font-size: var(--font-size-xl);
  font-weight: 600;
  font-family: var(--font-poppins);
  color: var(--color-gray-100);
  text-align: left;
}
.contact,
.home1:hover {
  cursor: pointer;
}
.contact {
  text-decoration: none;
  position: relative;
  font-size: var(--font-size-xl);
  font-weight: 600;
  font-family: var(--font-poppins);
  color: var(--color-gray-100);
  text-align: left;
}
.contact:hover,
.home1:hover {
  color: var(--color-crimson);
}
.menu-items {
  display: flex;
  flex-direction: row;
  padding: 0 var(--padding-4xl) 0 var(--padding-md);
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-2xl);
}
.hamburger,
.menu-container {
  flex-shrink: 0;
  margin-left: -30px;
}
.menu-container {
  width: 924px;
  height: 93px;
  display: flex;
  flex-direction: column;
  padding: var(--padding-xs) 0 var(--padding-xs) var(--padding-5xs);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}
.hamburger {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: relative;
  width: 122px;
  height: 98px;
  background-image: url(../images/rectangle34@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  display: none;
}
.navbar {
  align-self: stretch;
  background-color: var(--color-gray-200);
  display: flex;
  flex-direction: row;
  padding: var(--padding-4xs) 0;
  align-items: center;
  justify-content: flex-start;
  /* position: fixed; */
  /* width: 100%; */
}
@media screen and (max-width: 960px) {
  .menu-items {
    width: auto;
    height: auto;
    flex-direction: column;
    gap: 5px;
    padding: 0px;
    margin-left: -50px;
    align-items: end;
  }
  .hamburger {
    display: flex;
  }
  #logo-container{
    padding: 0px 91px 0px 19px;
  }
  .menu-container{
    display: none;
    flex-direction: column;
  }
}
