.team-admins1 {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  padding: 0 var(--padding-2xl);
  align-items: center;
  justify-content: center;
  gap: var(--gap-xs);
}
@media screen and (max-width: 1200px) {
  .team-admins1 {
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0px 0px;
    gap: 10px;
  }
}
@media screen and (max-width: 960px) {
  .team-admins1 {
    flex-direction: column;
  }
}
