@keyframes slide-in-left {
  0% {
    transform: translateX(-200px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes slide-in-right {
  0% {
    transform: translateX(200px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
.robocon1 {
  margin: 0;
  position: relative;
  font-size: inherit;
  font-weight: 600;
  font-family: inherit;
  display: inline-block;
  width: auto;
  height: 62.16px;
  flex-shrink: 0;
}
.the-abu-asia-pacific1 {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-base);
}
.robocon-group {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: var(--gap-4xs);
}
.know-more1 {
  position: relative;
  font-size: var(--font-size-3xl);
  font-weight: 600;
  font-family: var(--font-roboto);
  color: var(--color-gray-100);
  text-align: left;
}
.button1,
.right-container-para {
  display: flex;
  justify-content: center;
}
.button1 {
  cursor: pointer;
  border: 0;
  padding: var(--padding-2xs) var(--padding-md);
  background-color: var(--color-crimson);
  border-radius: var(--br-xl);
  flex-direction: row;
  align-items: center;
}
.right-container-para {
  align-self: stretch;
  flex: 1;
  border-radius: var(--br-lg);
  background-color: var(--color-gray-200);
  flex-direction: column;
  padding: var(--padding-4xs) var(--padding-2xs);
  align-items: flex-start;
  gap: var(--gap-2xs);
  opacity: 0;
}
.right-container-para.animate {
  animation: 1s ease 0s 1 normal forwards slide-in-left;
}
.image-icon1 {
  position: relative;
  border-radius: var(--br-md);
  width: 404px;
  height: 420px;
  flex-shrink: 0;
  object-fit: contain;
  opacity: 0;
}
.image-icon1.animate {
  animation: 1s ease 0s 1 normal forwards slide-in-right;
}
.right-container {
  align-self: stretch;
  background-color: var(--color-gray-100);
  display: flex;
  flex-direction: row;
  padding: var(--padding-sm) var(--padding-xl);
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-lg);
  text-align: left;
  font-size: var(--font-size-4xl);
  color: var(--color-gray-100);
  font-family: var(--font-poppins);
}
@media screen and (max-width: 960px) {
  .right-container-para {
    flex: unset;
    align-self: stretch;
  }
  .right-container {
    flex-direction: column;
    padding: 15px;
    gap: 5px
  }
  .image-icon1{
    width: 337px;
    height: 210px;
  }
  .button1{
    padding: 10px;
  }
  .know-more1{
    font-size: 12px;
  }
}
