@keyframes shadow-drop-bottom {
  0% {
    box-shadow: 0 0 0 0 transparent;
  }
  to {
    box-shadow: 0 12px 20px -12px rgb(0 0 0/35%);
  }
}
.jyoti-icon {
  position: relative;
  border-radius: var(--br-sm);
  width: 379px;
  height: 325px;
  flex-shrink: 0;
  object-fit: cover;
}
.jyoti-icon:hover {
  animation: 1s ease 0s infinite normal none shadow-drop-bottom;
  opacity: 1;
}
.crc1 {
  margin-block-start: 0;
  margin-block-end: 0;
}
.jyotiraditya-varute {
  margin: 0;
}
.crc-jyotiraditya-varute-container {
  position: relative;
}
.crc {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: var(--gap-md);
  text-align: center;
  font-size: var(--font-size-2xl);
  color: var(--color-black);
  font-family: var(--font-poppins);
}
@media screen and (max-width: 1200px) {
  .crc {
    flex: unset;
    align-self: stretch;
  }
}
@media screen and (max-width: 960px) {
  .crc {
    flex: unset;
    align-items: stretch;
    gap: 0px;
  }
  .jyoti-icon{
    height: 206px;
    width: 342px;
    margin-left: 15px;
  }
}
