@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@600&display=swap");

body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-roboto: Roboto;
  --font-poppins: Poppins;

  /* font sizes */
  --font-size-base: 20px;
  --font-size-lg: 22px;
  --font-size-xl: 24px;
  --font-size-2xl: 28px;
  --font-size-3xl: 32px;
  --font-size-4xl: 40px;
  --font-size-5xl: 48px;
  --font-mobile-para: 12px;
  --font-mobile-heading: 34px;

  /* Colors */
  --color-gray-100: #fdfffc;
  --color-gray-200: #011627;
  --color-gray-300: rgba(253, 255, 252, 0.53);
  --color-crimson: #e71d36;
  --color-black: #000;
  --color-burlywood: #fecc86;

  /* Gaps */
  --gap-4xs: 6px;
  --gap-3xs: 15px;
  --gap-2xs: 16px;
  --gap-xs: 20px;
  --gap-sm: 23px;
  --gap-md: 26px;
  --gap-lg: 29px;
  --gap-xl: 30px;
  --gap-2xl: 36px;
  --gap-3xl: 50px;

  /* Paddings */
  --padding-5xs: 4px;
  --padding-4xs: 10px;
  --padding-3xs: 19px;
  --padding-2xs: 20px;
  --padding-xs: 25px;
  --padding-sm: 30px;
  --padding-md: 32px;
  --padding-lg: 40px;
  --padding-xl: 45px;
  --padding-2xl: 63px;
  --padding-3xl: 71px;
  --padding-4xl: 97px;
  --padding-5xl: 120px;

  /* border radiuses */
  --br-2xs: 10px;
  --br-xs: 20px;
  --br-sm: 30px;
  --br-md: 37px;
  --br-lg: 40px;
  --br-xl: 41px;
}
