@keyframes slide-in-left {
  0% {
    transform: translateX(-200px);
    opacity: 0;
  }

  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slide-in-right {
  0% {
    transform: translateX(200px);
    opacity: 0;
  }

  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.image-icon,
.robocon {
  position: relative;
  flex-shrink: 0;
}

.image-icon {
  border-radius: var(--br-md);
  width: 404px;
  height: 420px;
  object-fit: cover;
  opacity: 0;
}

.image-icon.animate {
  animation: 1s ease 0s 1 normal forwards slide-in-left;
}

.robocon {
  margin: 0;
  font-size: inherit;
  font-weight: 600;
  font-family: inherit;
  display: inline-block;
  width: auto;
  height: 62.16px;
}

.the-abu-asia-pacific {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-base);
}

.robocon-parent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: var(--gap-4xs);
}

.know-more {
  position: relative;
  font-size: var(--font-size-3xl);
  font-weight: 600;
  font-family: var(--font-poppins);
  color: var(--color-gray-100);
  text-align: left;
}

.button {
  cursor: pointer;
  border: 0;
  padding: var(--padding-2xs) var(--padding-md);
  background-color: var(--color-gray-200);
  border-radius: var(--br-xl);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.left-container,
.left-container-para {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
}

.left-container-para {
  flex: 1;
  border-radius: var(--br-lg);
  background-color: var(--color-burlywood);
  flex-direction: column;
  padding: var(--padding-4xs) var(--padding-2xs);
  justify-content: center;
  gap: var(--gap-2xs);
  opacity: 0;
}

.left-container-para.animate {
  animation: 1s ease 0s 1 normal forwards slide-in-right;
}

.left-container {
  background-color: var(--color-gray-300);
  flex-direction: row;
  padding: var(--padding-sm) var(--padding-xl);
  justify-content: flex-start;
  gap: var(--gap-xl);
  text-align: left;
  font-size: var(--font-size-4xl);
  color: var(--color-gray-200);
  font-family: var(--font-poppins);
}

@media screen and (max-width: 960px) {
  .left-container-para {
    flex: unset;
    align-self: stretch;
  }

  .left-container {
    flex-direction: column;
    padding: 15px;
    gap: 5px;
  }

  .image-icon {
    width: 337px;
    height: 210px;
    object-fit: contain;
  }
  #repeater-heading{
    font-size: var(--font-mobile-heading);
  }
  #repeater-para{
    font-size: var(--font-mobile-para);
  }
  .button{
    padding: 10px;
  }
  .know-more{
    font-size: 12px;
  }
}