@keyframes fade-in {
  0% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.team-advisor-child {
  align-self: stretch;
  position: relative;
  border-radius: var(--br-xs);
  max-height: 100%;
  width: 178.5px;
  flex-shrink: 0;
  object-fit: cover;
}
.team-advisor-child:hover {
  animation: 1s ease 0s infinite normal none fade-in;
  opacity: 1;
}
.dr-sincy-george,
.phd-electrical-engg,
.team-advisor1 {
  position: absolute;
  top: 84.74px;
  left: 0;
  display: inline-block;
  width: auto;
  height: 110.26px;
}
.dr-sincy-george,
.team-advisor1 {
  top: 0;
  font-weight: 600;
  width: 483.01px;
  height: 43.9px;
}
.team-advisor1 {
  top: 43.9px;
  left: 1.15px;
  font-size: var(--font-size-lg);
  width: 158.76px;
  height: 29.61px;
}
.dr-sincy-george-parent {
  position: absolute;
  top: 0;
  left: 0;
  width: auto;
  height: 73.51px;
  font-size: var(--font-size-2xl);
}
.phd-electrical-engg-2000-parent {
  flex: 1;
  position: relative;
  height: 195px;
}
.team-advisor {
  flex: 1;
  border-radius: var(--br-sm);
  background-color: var(--color-gray-200);
  display: flex;
  flex-direction: row;
  padding: var(--padding-3xs) var(--padding-2xs);
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-xl);
  text-align: left;
  font-size: var(--font-size-base);
  color: var(--color-gray-100);
  font-family: var(--font-poppins);
}
@media screen and (max-width: 1200px) {
  .phd-electrical-engg-2000-parent,
  .team-advisor {
    flex: unset;
    align-self: stretch;
  }
  .team-advisor {
    flex-direction: column;
    gap: 10px;
    height: 346px;
  }
  .phd-electrical-engg{
    font-size: 12px;
    top: 64.74px;
  }
  .dr-sincy-george{
    font-size: 22px;
  }
  .team-advisor1{
    font-size: 18px;
  }
}
